import { Typography } from "@material-ui/core";
import { useEffect, useState } from "react";
import ProjectViewCard from "components/shared/Cards/Cards";
import SubHeader from "components/shared/SubHeader";

const Portfolio = () => {
  const [portfolio, setPortfolio] = useState([]);

  useEffect(() => {
    fetch("https://webpieapp.herokuapp.com/api/projects")
      .then((response) => response.json())
      .then((data) => {
        console.log("data ", data);
        setPortfolio(data.data);
      });
  }, []);

  return (
    <section className="portfolio-wrapper">
      <SubHeader
        title="Portfolio"
        keywords=""
        description="Our Portfolio"
        activePage={"Portfolio"}
      ></SubHeader>

      <section>
        <div className="container">
          <Typography variant="body1"> See our creative work</Typography>
          <ProjectViewCard portfolio={portfolio} />
        </div>
      </section>
    </section>
  );
};
export default Portfolio;
