import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import "./App.scss";
import CssBaseline from "@material-ui/core/CssBaseline";
import Dashboard from "components/admin/Dashboard";
import Home from "components/frontend/Home/Home";
import About from "components/frontend/About/About";
import Portfolio from "components/frontend/Portfolio/Portfolio";
import StickyFooter from "components/frontend/Global/Footer/Footer";

import Login from "./admin/Login";
import Header from "./frontend/Global/Header/Header";
import Contact from "./frontend/Contact/Contact";
import PrivacyPolicy from "./frontend/privacy-policy/PrivacyPolicy";
import TermsCondition from "./frontend/terms-condition/TermsCondition";

function App() {
  return (
    <React.Fragment>
      <CssBaseline />

      <Router>
        <div className="app-init">
          <Header />

          <section className="content-holder">
            {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
            <Switch>
              <Route path="/about">
                <About />
              </Route>
              {/* <Route path="/dashboard"></Route> */}

              <Route path="/panel">
                <Dashboard />
              </Route>

              <Route path="/login">
                <Login />
              </Route>

              <Route path="/portfolio">
                <Portfolio />
              </Route>

              <Route path="/privacy-policy" component={PrivacyPolicy} />
              <Route path="/privacy" component={PrivacyPolicy} />
              <Route path="/terms-and-conditions" component={TermsCondition} />

              <Route path="/contact" component={Contact} />

              <Route path="/">
                <Home />
              </Route>
            </Switch>
          </section>

          <StickyFooter />
        </div>
      </Router>
    </React.Fragment>
  );
}

export default App;
