import { Helmet } from "react-helmet";
import { useHistory } from "react-router-dom";

const Meta = (props) => {
  const history = useHistory();
  const path = history.location.pathname;
 return (
    <Helmet>
    <meta charSet="utf-8" />
    <title>
      {props.title ? props.title : "Webtechpie | The smart web solution."}
    </title>
    <meta
      name="description"
      content={
        props.description
          ? props.description
          : "Webtechpie is the Web Design and Brand Communication Agency in India offering Professional Digital Marketing services for your company."
      }
    />
    <meta
      name="keywords"
      content={
        props.keywords
          ? props.keywords
          : "Brand Communication, Brands,web design, Web design agency, professional, web development companies, web development, india, Digital Marketing, Services, professional web design company, Digital Marketing services, website design, web designing, marketing"
      }
    />
    <meta name="generator" content="Webtechpie" />
    <link rel="canonical" href={`https://www.webtechpie.com${path}`} />
  </Helmet>
 )
  
};

export default Meta;
