import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import clsx from "clsx";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Icon from "@material-ui/core/Icon";
import { useHistory } from "react-router";
const Header = (props) => {
  const history = useHistory();
  const logOut = () => {
    document.getElementById("root").querySelector(".app-init").classList.remove('active-panel');
    history.push('/login');
    localStorage.removeItem("token");
  }
  return (
    <section>
      <AppBar
        position="absolute"
        className={clsx(
          props.classes.appBar,
          props.open && props.classes.appBarShift
        )}
      >
        <Toolbar className={props.classes.toolbar}>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="open drawer"
            onClick={props.handleDrawerOpen}
            className={clsx(
              props.classes.menuButton,
              props.open && props.classes.menuButtonHidden
            )}
          >
            <Icon>menu</Icon>
          </IconButton>
          <Typography
            component="h1"
            variant="h6"
            color="inherit"
            noWrap
            className={props.classes.title}
          >
            Dashboard
          </Typography>
          <IconButton color="inherit" onClick={() => logOut()}>
            <Icon>logout</Icon>
          </IconButton>
        </Toolbar>
      </AppBar>
    </section>
  );
};

export default Header;
