import {
  Button,
  Grid,
  TextField,
  Typography,
  MenuItem,
  Select,
  FormHelperText,
} from "@material-ui/core";
import { Alert } from "@material-ui/lab";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import SubHeader from "components/shared/SubHeader";
const Contact = () => {
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();

  const [message, setMessage] = useState({});
  const [createButton, setCreateButton] = useState("Send");

  const onSubmit = (data, e) => {
    e.preventDefault();

    setCreateButton("Sending...");

    // adding formsubmit.co configuration for email
    data._subject = `${data.enquiry} | ${data.subject}`;
    // data._honey = "";
    data._template = "table"; // template
    data._cc = "hidaytraj@gmail.com";

    fetch("https://formsubmit.co/ajax/webtechpie@gmail.com", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
        setCreateButton("Email Sent");
        setMessage({
          value: data.message,
          type: "success",
        });

        setTimeout(() => {
          setCreateButton("Send");
        }, 2000);

        setTimeout(() => {
          setMessage("");
        }, 4000);
      })
      .catch((error) => {
        console.log(error);
        setCreateButton("Send");
        setMessage({
          value: error,
          type: "error",
        });
      });
  };

  return (
    <section className="contact-wrapper">
      <SubHeader
        title="Contact Us"
        keywords=""
        description="Reach Us | Webtechpie"
        activePage={"Contact Us"}
      ></SubHeader>
      <section className="">
        <div className="container">
          <form
            onSubmit={handleSubmit(onSubmit)}
            method="POST"
            action="e3b8249e76ece98e230179f5679118a5"
          >
              <h4> Need a quote, Query or Suggestion?</h4>

              <div className="row contact-form-row">
                <div className="col-sm-12">
                  <label>I am querying for </label>
                  <Select
                    labelId="demo-customized-select-label"
                    id="demo-customized-select"
                    name="enquiry"
                    {...register("enquiry", {
                      required: {
                        value: true,
                        message: "This is required",
                      },
                    })}
                  >
                    <MenuItem value={"enquiry"}>Enquiry</MenuItem>
                    <MenuItem value={"support"}>support</MenuItem>
                    <MenuItem value={"training"}>Training</MenuItem>
                    <MenuItem value={"sales"}>Sales</MenuItem>
                  </Select>
                  <FormHelperText>
                    {errors.enquiry && "Please choose category"}
                  </FormHelperText>
                </div>

                <div className="col-sm-6">
                  <TextField
                    label="Please write a subject"
                    type="text"
                    defaultValue=""
                    fullWidth
                    name="subject"
                    variant="outlined"
                    {...register("subject", {
                      required: {
                        value: true,
                        message: "This is required",
                      },
                      maxLength: {
                        value: 50,
                        message: "Not allowed more then 50",
                      },
                      minLength: {
                        value: 10,
                        message: "Atleast 10 characters",
                      },
                    })}
                  />
                  {errors.name && (
                    <Alert severity="error">{errors.name?.message}</Alert>
                  )}
                </div>

                <div className="col-sm-6">
                  {/* register your input into the hook by invoking the "register" function */}
                  <TextField
                    label="Whats your good name?"
                    type="text"
                    defaultValue=""
                    fullWidth
                    name="name"
                    variant="outlined"
                    {...register("name", {
                      required: {
                        value: true,
                        message: "This is required",
                      },
                    })}
                  />
                  {errors.name && (
                    <Alert severity="error">{errors.name?.message}</Alert>
                  )}
                </div>

                <div className="col-sm-6">
                  {/* register your input into the hook by invoking the "register" function */}
                <TextField
                  label="Where we can get back to you?"
                  type="email"
                  defaultValue=""
                  fullWidth
                  name="email"
                  variant="outlined"
                  {...register("email", {
                    required: {
                      value: true,
                      message: "This is required",
                    },
                  })}
                />
                {errors.name && (
                  <Alert severity="error">{errors.name?.message}</Alert>
                )}
                </div>

                <div className="col-sm-6">
                <TextField
                  label="Can we call you?"
                  type="number"
                  defaultValue=""
                  fullWidth
                  name="mobile"
                  variant="outlined"
                  {...register("mobile", {
                    minLength: {
                      value: 10,
                      message:
                        "Please provide valid mobile number can be atleast 10 digits",
                    },
                  })}
                />
                {errors.mobile && (
                  <Alert severity="error">{errors.mobile?.message}</Alert>
                )}
                </div>

                <div className="col-sm-6">
                <TextField
                  label="Message"
                  multiline
                  rows={4}
                  fullWidth
                  name="message"
                  defaultValue=""
                  variant="outlined"
                  {...register("message", {
                    required: {
                      value: true,
                      message: "Please write the query in brief.",
                    },
                  })}
                />
                {errors.message && (
                  <Alert severity="error">{errors.message?.message}</Alert>
                )}
                </div>

                <div className="col-sm-12">
                <Button
                  variant="contained"
                  color={message.type === "info" ? "secondary" : "primary"}
                  type="submit"
                >
                  {createButton}
                </Button>
                </div>

                <div className="col-sm-12">
                {message.value && (
                  <Alert severity={message.type}>{message.value}</Alert>
                )}
                </div>
              </div>

          </form>
        </div>
      </section>
    </section>
  );
};

export default Contact;
