import { Link, useRouteMatch } from "react-router-dom";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Box from "@material-ui/core/Box";
import Orders from "../Orders";
import Title from "../Title";

import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import { useEffect, useState } from "react";
import { CircularProgress, Divider, Icon } from "@material-ui/core";
import { Alert } from "@material-ui/lab";

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
  },
});

const Projects = (props) => {
  const match = useRouteMatch();
  const classes = useStyles();
  const [project, setProject] = useState([]);
  const [message, setMessage] = useState({});
  const [progress, setProgress] = useState(false);

  useEffect(() => {
    setProgress(true);

    fetch("https://webpieapp.herokuapp.com/api/projects")
      .then((response) => response.json())
      .then((data) => {
        console.log(data);

        if (data.data.length > 0) {
          setProject(data.data);
          setMessage({
            message: data.message,
            type: "success",
          });
        } else {
          setMessage({
            message: data.message,
            type: "info",
          });
        }

        setProgress(false);
      });
  }, []);

  const deleteHandler = (id) => {
    const confirmDelete = window.confirm("Are you Sure?");

    if (confirmDelete) {
      // Simple DELETE request with fetch
      fetch(`https://webpieapp.herokuapp.com/api/projects/${id}`, {
        method: "DELETE",
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      }).then((data) => {
        console.log("data wah  ", data);

        setMessage({
          message: data.message,
          type: "success",
        });
      });
    }
  };

  return (
    <section className="projects-wrapper">
      <Container maxWidth="md" style={{
          padding: "20px",
          marginTop: "3rem",
          background: "#fff",
        }}>
        <Typography variant="h2" color="textSecondary" align="left">
          Projects
        </Typography>
        <br />

        <Link to="/panel/addProject">
          <Button size="small" variant="contained" color="secondary">
            Add new <Icon>add</Icon>
          </Button>
        </Link>

        <br />
        <br />
        <hr />
        <br />
        <Grid
          container
          spacing={3}
          direction="row"
          alignItems="center"
          alignContent="center"
        >
          {/* All Projects */}
          {!progress && project && project.length > 0
            ? project.map((project) => {
                return (
                  <Grid item md={6}>
                    <Card className={classes.root}>
                      <CardActionArea>
                        <CardMedia
                          component="img"
                          alt="Contemplative Reptile"
                          height="140"
                          image={project.image}
                          title="Contemplative Reptile"
                        />
                        <CardContent>
                          <Typography gutterBottom variant="h5" component="h2">
                            {project.name}
                          </Typography>
                          <Typography
                            variant="body2"
                            color="textSecondary"
                            component="p"
                          >
                            {project.description}
                          </Typography>
                        </CardContent>
                      </CardActionArea>
                      <CardActions>
                        <Button
                          size="small"
                          variant="contained"
                          color="primary"
                        >
                        <Icon>edit</Icon> Update
                        </Button>

                        <Button size="small" color="primary">
                          Visit
                        </Button>

                        <Button
                          size="small"
                          variant="contained"
                          color="secondary"
                          onClick={() => deleteHandler(project._id)}
                        >
                          <Icon>delete</Icon> Delete
                        </Button>

                      </CardActions>
                    </Card>
                  </Grid>
                );
              })
            : message.message && (
                <Alert
                  severity={message.type}
                  style={{
                    width: "100%",
                  }}
                >
                  {message.message}
                </Alert>
              )}

          {progress ? <CircularProgress color="secondary" /> : null}
        </Grid>
        <Box pt={4}>{/* <Copyright /> */}</Box>
      </Container>
    </section>
  );
};
export default Projects;
