import { Button, Grid, TextField, Typography } from "@material-ui/core";
import { Alert } from "@material-ui/lab";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router";

export default function ProjectManagement() {
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();

  const [message, setMessage] = useState({});
  const [createButton, setCreateButton] = useState("Create");
  const history = useHistory();

  console.log(" errors ", errors);

  const onSubmit = (data) => {
    console.log(data);
    setCreateButton("Creating...");

    const formData = new FormData();

    formData.append("image", data.image[0]);
    formData.append("name", data.name);
    formData.append("link", data.link);
    formData.append("description", data.description);
    formData.append("technologies", data.technologies);

    // files.map((file, index) => {
    //   formData.append(`file${index}`, file);
    // });

    fetch("https://webpieapp.herokuapp.com/api/projects", {
      // content-type header should not be specified!
      method: "POST",
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
      body: formData,
    })
      .then((response) => response.json())
      .then((data) => {
        // Do something with the successful response
        console.log("data ", data);

        if (data.name) {
          setCreateButton("Created");
          setMessage({
            value: `${data.name} has been added`,
            type: "success",
          });

          setTimeout(() => {
            history.push("/panel/projects");
          }, 2000);
        } else {
          setMessage({
            value: data.message,
            type: "info",
          });
        }
      })
      .catch((error) => {
        setMessage(error);
        setMessage({
          value: error,
          type: "error",
        });
        console.log(error);
      });
  };

  console.log(watch("name")); // watch input value by passing the name of it

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Grid
        container
        direction="column"
        justify="center"
        alignItems="left"
        spacing={2}
        style={{
          padding: "20px",
          marginTop: "1rem",
          background: "#fff",
        }}
      >
        <Typography variant="h4" color="textSecondary" align="left">
          Add new Projects
        </Typography>
        <Grid item xs={12}>
          {/* register your input into the hook by invoking the "register" function */}
          <TextField
            label="Enter project name"
            type="text"
            defaultValue=""
            fullWidth
            variant="outlined"
            {...register("name", {
              required: {
                value: true,
                message: "This is required",
              },
              maxLength: {
                value: 100,
                message: "Not allowed more then 100",
              },
              minLength: {
                value: 10,
                message: "Atleast 10 characters",
              },
            })}
          />
          {errors.name && (
            <Alert severity="error">{errors.name?.message}</Alert>
          )}
        </Grid>

        <Grid item>
          {/* include validation with required or other standard HTML validation rules */}
          <TextField
            label="Enter project URL"
            type="link"
            fullWidth
            variant="outlined"
            defaultValue=""
            {...register("link", { required: true })}
          />
          {errors.link && (
            <Alert severity="error">{errors.link?.message}</Alert>
          )}
        </Grid>

        <Grid item>
          {/* include validation with required or other standard HTML validation rules */}
          <Button variant="contained" component="label">
            Select Project Image
            <input
              type="file"
              hidden
              {...register("image", { required: true })}
            />
          </Button>
          {errors.image && (
            <Alert severity="error">{errors.image?.message}</Alert>
          )}
        </Grid>

        <Grid item>
          <TextField
            label="Description"
            multiline
            rows={4}
            fullWidth
            defaultValue="This project belongs to someone else"
            variant="outlined"
            {...register("description", { required: true })}
          />
          {errors.description && (
            <Alert severity="error">{errors.description?.message}</Alert>
          )}
        </Grid>

        <Grid item>
          <TextField
            label="technologies"
            defaultValue=""
            variant="outlined"
            fullWidth
            {...register("technologies", { required: true })}
          />
          {errors.technologies && (
            <Alert severity="error">{errors.technologies?.message}</Alert>
          )}
        </Grid>

        <Grid item>
          <Button
            variant="contained"
            color={message.type === "info" ? "secondary" : "primary"}
            type="submit"
          >
            {createButton}
          </Button>
        </Grid>

        <Grid item>
          {message.value && (
            <Alert severity={message.type}>{message.value}</Alert>
          )}
        </Grid>
      </Grid>
    </form>
  );
}
