import { useForm } from "react-hook-form";
import { Grid, TextField, Button, Typography } from "@material-ui/core";
import { Alert, AlertTitle } from "@material-ui/lab";
import { useHistory } from "react-router-dom";
import axios from "axios";
import { useState } from "react";

import {hidayt} from "core/utils/DashboardUtils"

export default function Login() {
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();

  const [message, setMessage] = useState();
  const [hasError, setHasError] = useState(false);

  const [loginStatus, setLoginStatus] = useState("Login");

  const history = useHistory();

  // check if user is logged in
  const authCheck = () => {
    if (localStorage.getItem("token")) {
      history.push("/panel/dashboard");
    }
  };

  authCheck();

  const onSubmit = (data) => {
    console.log(data);
    setMessage("Signing in...");
    setLoginStatus("Please wait...");
    fetch("https://webpieapp.herokuapp.com/api/user/login", {
      method: "POST", // or 'PUT'
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((data) => {
        setHasError(true);
        setMessage("Give us a moment.");
        const hasError = "error" in data && data.error != null;

        console.log("Success:", data);

        // check token and set it in the browser and redirect to the dashboard
        if (data.accessToken) {
          setLoginStatus("Wait for a moment");
          localStorage.setItem("token", data.accessToken);
          history.push("/panel/dashboard");
          document
            .getElementById("root")
            .querySelector(".app-init")
            .classList.add("active-panel");
        }

        setMessage(data.message);
        setLoginStatus("Login");
      })
      .catch((error) => {
        setLoginStatus("Login");
        console.error("Cusrom Error:", error);
        
        setMessage(error);
      });
  };

  //console.log(watch("example")); // watch input value by passing the name of it

  return (
    <div className="login-wrapper">
      <form onSubmit={handleSubmit(onSubmit)}>
        <Grid
          container
          direction="column"
          justify="center"
          alignItems="center"
          spacing={2}
          style={{
            padding: "20px",
            marginTop: "3rem",
          }}
        >
          <Typography variant="h3">Login</Typography>
          <Grid item>
            {/* register your input into the hook by invoking the "register" function */}
            <TextField
              label="Enter username"
              type="email"
              defaultValue=""
              helperText={
                errors.username && <span>This field is required</span>
              }
              {...register("username", { required: true })}
            />
          </Grid>

          <Grid item>
            {/* include validation with required or other standard HTML validation rules */}
            <TextField
              label="Enter password"
              type="password"
              defaultValue=""
              helperText={
                errors.password && <span>This field is required</span>
              }
              {...register("password", { required: true })}
            />
          </Grid>

          <Grid item>
            <Button variant="contained" color="primary" type="submit">
              {loginStatus}
            </Button>
          </Grid>

          <Grid item>
            <Alert severity="error">{message}</Alert>
          </Grid>
        </Grid>
      </form>
    </div>
  );
}
