import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import { Button, Grid, Icon } from '@material-ui/core';

import { dateFormat, openUrl } from "core/utils/Utils";

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 345,
        flexGrow: 1,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
}));

export default function ProjectViewCard(props) {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };

    const correctionURL = (url) => {
        return "https://webpieapp.herokuapp.com/project/images/1622046238517.png" //url.replace('https://webpieapp.herokuapp.comproject','https://webpieapp.herokuapp.com/project')
    }


    return (
        <section>

            <Grid container spacing={3}
                direction="row"
                justify="center"
                alignItems="center">

                {
                    props.portfolio && props.portfolio.length > 0 ?
                        props.portfolio.map((item, index) => {
                            return (
                                <Grid item sm={6} md={4} key={item._id}
                                >
                                    <Card className={classes.root}>
                                        <CardHeader
                                            action={
                                                <IconButton aria-label="settings">
                                                    <Icon>more_vert</Icon>
                                                </IconButton>
                                            }
                                            title={item.name}
                                            subheader={dateFormat(item.created)}
                                        />
                                       
                                        <CardContent>
                                            <Typography variant="body2" color="textSecondary" component="p">
                                                {item.description}
                                            </Typography>
                                        </CardContent>
                                        <CardActions disableSpacing>

                                            <Grid container space={3}>
                                                <Grid item xs={12} sm={6}>
                                                    <Button variant="contained" color="primary" onClick={e => openUrl(item.link)}>Visit</Button>
                                                </Grid>

                                                {/* <Grid item xs={12} sm={6}>
                                                    <Button variant="contained" color="secondary">
                                                        Demo </Button>
                                                </Grid> */}

                                            </Grid>




                                            <IconButton
                                                className={clsx(classes.expand, {
                                                    [classes.expandOpen]: expanded,
                                                })}
                                                onClick={handleExpandClick}
                                                aria-expanded={expanded}
                                                aria-label="show more"
                                            >
                                                <Icon>unfold_more</Icon>
                                            </IconButton>
                                        </CardActions>
                                        <Collapse in={expanded} timeout="auto" unmountOnExit>
                                            <CardContent>
                                                <Typography paragraph>
                                                    {item.description}
          </Typography>

                                     
                                            </CardContent>
                                        </Collapse>
                                    </Card>
                                </Grid>

                            )
                        })
                        : null
                }

            </Grid>



        </section>

    );
}